}
.loading.show {
  display: block;
}
.loading .overlay-loading {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9;
  background: #191919;
}
.loading .overlay-loading .loading-ui {
  display: flex;
  align-items: center;
  gap: 0.625rem;
  color: #fff;
  font-weight: bold;
  text-transform: uppercase;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.loading .overlay-loading .loading-ui::after {
  content: "";
  display: block;
  min-width: 1rem;
  border-radius: 100%;
  border: 0.3125rem solid #fff;
  border-top-color: transparent;
  animation: loading-game 0.5s linear infinite;
}
@keyframes loading-game {
  to {
    transform: rotate(360deg);
  }
}
@media screen and (min-width: 480px) {
  .loading .overlay-loading .loading-ui {
    font-size: 1.75rem;
  }
  .loading .overlay-loading .loading-ui::after {
    width: 1.5625rem;
    height: 1.5625rem;
  }
}
@media screen and (max-width: 479px) {
